.social__conteiner {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  .page-footer__text {
    font-family: PT Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 21px;
    line-height: 26px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    margin: 0 auto;
  }
  .social__list {
    padding: 0;
  }
  .page-footer__social {
    margin: 0 auto;
  }
  .copyright {
    margin-top: 45px;
    display: flex;
    justify-content: center;
  }
  .copyright__text {
    line-height: 26px;
  }
  .copyright__link {
    height: 34px;
    padding-top: 10px;
  }
  @media (min-width: 768px) {
    .social__conteiner{
      display: flex;
      flex-direction: row;
      justify-content: space-around;
      height: 120px;
    }
    .page-footer__text {
      margin: 0;
      padding-top: 40px;
    }
    .page-footer__social {
      margin: 0;
    }
    .copyright {
      margin: 0;
      padding-top: 30px;
    }
  }
  