.page {
    height: 100%;
    font-size: 14px;
    line-height: 26px;
    font-family: "PT Sans", "Arial", sans-serif;
  }
  
  .page__body {
    align-content: start;
    min-width: 320px;
    min-height: 100%;
    margin: 0 auto;
    color: #000;
    text-transform: uppercase;
  }
  
  .button {
    display: inline-block;
    vertical-align: middle;
    font-size: 21px;
    line-height: 26px;
    color: #fff;
    background-color: #81b3d2;
    padding: 14px 32px 14px 32px;
    border: none;
  }
  input {
    border: none;
  }
  @media (min-width: 768px) {
    .page-main {
      width: 768px;
      margin: 0 auto;
    }
  }
  @media (min-width: 1360px) {
    .page {
      background: #eee;
    }
    .page__body {
      width: 1200px;
      background: #fff;
    }
    .page-main {
      width: 1200px;
    }
  }
  