.title {
    font-family: PT Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
    color: #000000;
}

.page-form {
    width: 290px;
}
.review__text {
    resize: none;
    background: #eee;
    border: none;
    outline: none;
    width: 256px;
    height: 160px;
    font-family: PT Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
    padding: 21px;
}
.first-name__input {
    width: 70%;
    height: 70px;
    background: #eee;
    color: #000;
    padding: 10px 0 10px 14px;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
}

.second-name__input {
    width: 56%;
    height: 70%;
    background: #eee;
    color: #000;
    padding: 10px 0 10px 14px;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
}

.last-name__input {
    width: 58%;
    height: 70%;
    background: #eee;
    color: #000;
    padding: 10px 0 10px 14px;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
}

.contacts__phone {
    position: relative;
    font-family: PT Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 26px;
    align-items: center;
    text-transform: uppercase;
    color: grey;
}

.phone,
.email {
    width: 70%;
    height: 70%;
    background: #eee;
    color: #000;
    padding: 10px 0 10px 14px;
    font-size: 16px;
    font-weight: bold;
    font-style: normal;
}

.contacts__email {
    position: relative;
}

.phone__button,
.email__button {
    position: absolute;
    top: 21px;
    left: 203px;
    background: #E6E6E6;
    width: 40px;
    height: 44px;
    border: none;
}

.form__radio {
    width: 225px;
}
input:checked + .radio__span::before {
    content: '';
    opacity: 1;
}

.radio {
    font-family: PT Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
    align-items: center;
}

.radio__span {
    border: solid 1px #eee;
    background: #eee;
}

.radio__span:hover {
    opacity: 1;
    background: grey;
}

.radio .radio__span {
    position: relative;
    display: inline-block;
    width: 48px;
    height: 48px;
    top: 15px;
    background-repeat: no-repeat;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.radio input:checked + .radio__span::before {
    content: '';
    position: absolute;
    left: 15px;
    top: 15px;
    background: #030303;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    -webkit-border-radius: 50%;
    -moz-border-radius: 50%;
    -ms-border-radius: 50%;
    -o-border-radius: 50%;
}

.checkbox {
    font-family: PT Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 26px;
    align-items: center;
}

.form__checkbox {
    display: flex;
    flex-direction: column;
}

.checkbox__span:hover {
    opacity: 1;
}

.checkbox .checkbox__span {
    position: relative;
    display: inline-block;
    width: 46px;
    height: 46px;
    top: 15px;
    background: #eee;
}

.checkbox input:checked + span::before {
    content: '';
    position: absolute;
    left: 15px;
    top: 15px;
    width: 23px;
    height: 17px;
    background-image: url('../image/tick.svg');
}
@media (min-width: 768px) {
    .page-form {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin-bottom: 60px;
    }
    .form__name {
        width: 44%;
        order: 2;
        text-align: end;
    }

    .form__title {
        margin-bottom: 50px;
        font-size: 21px;
        width: 290px;
    }

    .form__title-big {
        margin-bottom: 50px;
        padding-right: 46px;
        padding-left: 46px;
        font-size: 21px;
    }

    .first-name__input {
        width: 216px;
        height: 36px;
        margin-bottom: 20px;
    }

    .second-name__input {
        width: 178px;
        height: 36px;
        margin-bottom: 20px;
    }

    .last-name__input {
        width: 180px;
        height: 36px;
        margin-left: 4px;
    }

    .form__contacts {
        order: 3;
        border-top: 1px solid #DBDBDB;
        width: 648px;
        margin-top: 70px;
    }

    .phone {
        margin-top: 8px;
        margin-bottom: 25px;
        width: 648px;
        height: 40px;
    }
    .email {
        margin-top: 8px;
        margin-bottom: 25px;
        width: 648px;
        height: 40px;
    }

    .phone__button  {
        top: 30px;
        left: 604px;
        width: 59px;
        height: 59px;
    }
    .email__button {
        top: 30px;
        left: 604px;
        width: 59px;
        height: 59px;
    }

    .form__radio {
        order: 1;
        width: 35%;
        border-right: 1px solid #DBDBDB;
    }

    .form__checkbox {
        order: 4;
        border-top: 1px solid #DBDBDB;
        width: 648px;
        margin-top: 70px;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
    }

    .checkbox {
        width: 40%;
    }

    .form__review {
        order: 5;
        margin-top: 70px;
        border-top: 1px solid #DBDBDB;
        width: 648px;
    }
    .review__text {
        width: 648px;
    }
    .form__review-button {
        width: 50%;
        height: 58px;
        border: none;
        margin-top: 58px;
    }
}
@media (min-width: 1360px) {
    .page-form {
        width: 960px;
        margin: 0 auto;
        margin-bottom: 60px;
    }
    .form__name {
        width: 50%;
    }
    .first-name__input {
        width: 350px;
        height: 45px;
    }

    .second-name__input {
        width: 312px;
        height: 45px;
        margin-left: 20px;
    }

    .last-name__input {
        width: 314px;
        height: 45px;
    }

    .form__radio {
        width: 32%;
        padding-right: 35px;
    }
    .form__checkbox {
        flex-wrap: nowrap;
        width: 960px;
    }
    .review__text {
        width: 960px;
        margin-bottom: 58px;
    }
}
