body {
    margin: 0;
    padding: 0;
}
.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    white-space: nowrap;
    clip-path: inset(100%);
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
  p {
    font-family: PT Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 21px;
    align-items: center;
    text-transform: uppercase;
  }
  li {
    list-style: none;
  }
  a {
    text-decoration: none;
  }
  fieldset {
    border: none;
  }
  ul {
    margin: 0;
    padding: 0;
  }
  .page__body {
    margin: 0 auto;
  }
  