.header__background {
    position: relative;
    margin-bottom: 30px;
    background-image: url('../image/back-mobile.jpg');
    width: 320px;
    height: 390px;
    background-repeat: no-repeat;
  }
  .header__logo {
    background-image: url('../image/logo-mobile.svg');
    width: 102px;
    height: 83px;
    background-repeat: no-repeat;
  }
  .page-header {
    position: relative;
  }
  .background__image-text {
    position: absolute;
    top: 85px;
    left: 15px;
  }
  .background__image-text-form {
    position: absolute;
    top: 45%;
    left: 20%;
  }
  .background__vector-left {
    position: absolute;
    bottom: 0;
  }
  .background__vector-right {
    position: absolute;
    bottom: 0;
    right: -1px;
  }
  .form-header__background {
    position: relative;
    margin-bottom: 30px;
    background-image: url('../image/back-form.jpg');
    width: 320px;
    height: 215px;
    background-repeat: no-repeat;
  }
  .form-background__vector-left {
    position: absolute;
    bottom: 0;
  }
  .form-background__vector-right {
    position: absolute;
    bottom: 0;
    right: 0;
  }
  @media (min-width: 768px) {
    .page-header__logo {
      position: absolute;
      left: 45%;
      z-index: 1;
    }
    .header__background {
      background-image: url('../image/back-desktop.jpg');
      width: 100%;
      height: 518px;
      background-repeat: no-repeat;
    }
    .background__image-text {
      top: 70px;
      left: 165px;
      width: 60%;
    }
    .background__vector-left,
    .background__vector-right {
      bottom: -1px;
      width: 84%;
    }
    .form-header__background {
      background-image: url(../image/back-form-desktop.png);
      background-repeat: no-repeat;
      width: 100%;
      height: 350px;
      background-size: 100%;
    }
    .background__image-text-form {
      width: 60%;
    }
    .form-background__vector-left,
    .form-background__vector-right {
      width: 86%;
    }
  }
  @media(min-width: 1360px) {
    .page-header__logo {
      position: absolute;
      left: 42%;
      z-index: 1;
    }
    .header__logo {
      background-image: url(../image/logo-desktop.svg);
      width: 186px;
      height: 100px;
    }
    .background__image-text {
      top: 12%;
      left: 26%;
      width: 45%;
    }
    .background__vector-left {
      width: 85%;
    }
    .background__vector-right {
      bottom: 0;
      width: 85%;
    }
  }
  