.gallery {
    width: 320px;
    text-align: center;
}
.gallery__list {
    margin: 0;
    padding: 0;
  }
  .gallery__item {
    border-top: 1px solid #e6e6e6;
    padding-top: 20px;
  }
  .gallery__item:last-child {
    border-bottom: 1px solid #e6e6e6;
  }
  .gallery__image {
      margin: 0 auto;
  }
  .gallery__image-first {
      background-image: url('../image/photo-mobile.jpg');
      width: 280px;
      height: 280px;
      margin: 0 auto;
  }
  .gallery__description {
    width: 200px;
  }
  .gallery__title {
      text-align: left;
  }
  .gallery__autor {
      text-align: left;
  }
  .like {
      width: 85px;
  }
  .like__image {
      padding-right: 14px;
  }
  .gallery__image-second {
      background-image: url('../image/photo-mobile-2.jpg');
      width: 280px;
      height: 280px;
  }
  .gallery__image-three {
      background-image: url('../image/photo-mobile-3.jpg');
      width: 280px;
      height: 280px;
  }
  .gallery__image-four {
      background-image: url('../image/photo-mobile-4.jpg');
      width: 280px;
      height: 280px;
  }
  @media (min-width: 768px) {
    .gallery {
        width: 648px;
        margin: 0 auto;
    }
    .gallery__list {
        width: 648px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }
    .gallery__item {
        border: none;
        width: 324px;
        padding: 0;
    }
    .gallery__item-first {
        margin-bottom: -14px;
    }
    .gallery__image-first {
        background-image: url('../image/photo-tables.jpg');
        width: 648px;
        height: 400px;
    }
    .gallery__description {
        height: 80px;
    }
    .gallery__description-first {
        width: 648px;
        margin-top: -18px;
        background: #F2F2F2;
        display: flex;
        justify-content: space-between;
    }
    .like {
        align-self: center;
        width: 100px;
    }
    .like__image {
        padding: 0;
    }
    .like__btn {
        width: 50px;
        border: none;
        background: #e6e6e6;
    }
    .gallery__image {
        width: 324px;
        margin-bottom: -10px;
    }
    .gallery__description {
        width: 324px;
        display: flex;
        justify-content: space-between;
        background: #e6e6e6;
        margin: 0;
        height: 80px;
    }
    .gallery__title {
        margin: 10px 0 0 0;

    }
    .gallery__autor {
        margin: 0;
    }
    .gallery__item:last-child {
        margin-right: auto;
    }
    .gallery__image-second {
        background-image: url(../image/photo-tables-2.jpg);
        width: 324px;
        height: 267px;
    }
    .gallery__image-three {
        background-image: url('../image/photo-tables-3.jpg');
        width: 324px;
        height: 267px;
    }
    .gallery__image-four {
        background-image: url('../image/photo-tables-4.jpg');
        width: 324px;
        height: 267px;
    }
  }
  @media (min-width:1360px) {
      .gallery__item-first {
        position: relative;
        margin-bottom: 0;
      }
      .gallery__image-first {
        background-image: url('../image/photo-desktop.jpg');
        width: 1200px;
        height: 400px;
      }
      .gallery__description-first {
        background: rgba(250, 250, 250, 0.1);
        position: absolute;
        bottom: 0;
        right: -7px;
        width: 1200px;
        margin: 0;
        color: #fff;
      }
      .like {
          width: 120px;
      }
      .like__svg {
          fill: #fff;
          opacity: 1;
      }
      .like__btn {
          border: none;
          background-color: rgba(250,250,250, 0,1);
          color: white;
      }
      .gallery {
          width: 1200px;
      }
      .gallery__list {
          width: 1200px;
      }
      .gallery__item {
        width: 400px;
        position: relative;
      }
      .gallery__image-second {
          background-image: url('../image/photo-desktop-2.jpg');
          width: 400px;
          height: 267px;
      }
      .gallery__image-three {
        background-image: url('../image/photo-3-desktop.jpg');
        width: 400px;
        height: 267px;
    }
    .gallery__image-four {
        background-image: url('../image/photo-4-desktop..jpg');
        width: 400px;
        height: 267px;
    }
    .gallery__description {
        background: rgba(250, 250, 250, 0.1);
        position: absolute;
        bottom: 0;
        right: -7px;
        width: 400px;
        margin: 0;
        color: #fff;
      }
  }
  