.search {
    text-align: center;
  }
  .search__title,
  .search__text {
    text-align: center;
  }
  .search__button {
    font-family: PT Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 26px;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
  }
  .map {
    background-image: url('../image/map-mobile.png');
    width: 320px;
    height: 350px;
    margin-top: 49px;
    margin-bottom: 38px;
  }
  @media (min-width: 768px) {
    .search {
      font-family: PT Sans;
      font-style: normal;
      font-weight: bold;
      font-size: 30px;
      line-height: 26px;
      align-items: center;
      text-align: center;
      text-transform: uppercase;
      margin-bottom: 58px;
    }
    .search__text {
        padding: 58px 166px;
    }
    .map {
      background-image: url('../image/map-tablet.jpg');
      width: 100%;
      margin: 0;
    }
  }
  @media (min-width: 1360px) {
    .search__text {
      width: 400px;
      margin: 0 auto;
  }
  
  }
  