.main-nav {
    background: #81B3D2;
  }
  .main-nav__list {
    list-style: none;
    margin: 0;
  }
  .main-nav__link {
    text-decoration: none;
    color: #fff;
    padding-bottom: 10px;
    width: 146px;
    height: 40px;
    font-family: PT Sans;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 40px;
    display: flex;
    align-items: center;
    text-align: center;
    text-transform: uppercase;
  }
  .main-nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 70px;
    height: 50px;
    background-color: #fff;
    cursor: pointer;
    border: none;
    &:hover,
    &:active {
      background-color: #fff;
    }
    .main-nav__list a {
      color: #ffffff;
    }
  }
  .site-list__item {
    background-color: #81B3D2;
  }
  .site-list__item:hover {
    background-color: #669EC0;
  }
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 36px;
    height: 2px;
    background-color: #81b2d3;
    box-shadow: 0 8px 0 0 #81b2d3, 0 16px 0 0 #81b2d3;
  }
  
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: fade(#ffffff, 30%);
    box-shadow:
      0 8px 0 0 fade(#ffffff, 30%),
      0 16px 0 0 fade(#ffffff, 30%);
  }
  .main-nav--opened .main-nav__toggle {
    top: 0;
    right: 0;
    left: auto;
  
    &::before,
    &::after {
      content: "";
      position: absolute;
      top: 24px;
      left: 18px;
      width: 36px;
      height: 2px;
      background-color: #81b2d3;
    }
  
    &::before {
      transform: rotate(45deg);
      box-shadow: none;
    }
  
    &::after {
      transform: rotate(-45deg);
    }
  }
  .main-nav--closed .site-list__item {
    display: none;

    &--active {
      display: block;
    }
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 50px;
    right: 0;
    left: 0;
    z-index: 10;
    min-height: 80vh;
  }

  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px fade(#000000, 40%);
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
    
  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
    margin: 0 auto;
  }

  .main-nav--nojs .site-list__item {
    display: block;
    width: 140px;
  }
