  @media (min-width: 768px) {
    .site-list__item:hover {
      background: none;
    }
    .site-list__item:active {
      background: none;
  }
    .main-nav {
      background: none;
      height: 56px;
      padding-bottom: 20px;
    }
    .main-nav__list {
      display: flex;
      justify-content: space-around;
    }
    .main-nav__item:first-child {
      width: 110px;
      margin-left: 50px;
    }
      .main-nav__link {
      color: #000;
    }
    .main-nav__link:first-child {
      color: #000;
    }
    .main-nav__item:nth-child(2) {
      margin-right: 120px;
    }
    .main-nav__toggle {
      opacity: 0;
      width: 1px;
      height: 1px;
      position: absolute;
      top: 3px;
      right: 40px;
    }
 
    .main-nav--closed {
      background-color: #fff;
    }
    .site-list__item {
      background: none;
    }
    .main-nav__wrapper {
      position: relative;
      min-height: 100px;
    }
  }
  @media (min-width: 1360px) {
    .site-list__item:hover {
      background-color: none;
    }
  }